import React, {useState} from 'react'
import './Login.css'

function Login(props) {
    const [userName,setUserName] = useState("");
    const [password,setPassword] = useState("");

    const handleUserNameChange = function(event){
        setUserName(event.target.value);
    };
    const handlePasswordChange = function(event){
        setPassword(event.target.value);
    };

    const handleLogin = function(){
        if(userName!="" && password!="")
            props.handleLogin({userName,password});
        else
            alert('All fields are compulsory!');
    };

    return (
        <div className="login-page col-md-12 col-sm-12 col-lg-12">
            <div className="row col-md-12 col-sm-12 col-lg-12">
                <div className="col-sm-4 col-lg-4 col-md-4"></div>
                <div className="title col-sm-4 col-lg-4 col-sm-4">EasyMaintain Login</div>
                <div className="col-sm-4 col-lg-4 col-md-4"></div>
            </div>
            <div className="row col-md-12 col-sm-12 col-lg-12">
                <div className="col-sm-4 col-lg-4 col-md-4"></div>
                <div className="col-sm-4 col-lg-4 col-md-4 row">
                    <div className="col-sm-6 col-lg-6 col-md-6 fields">
                        <label className="labelClass">UserName</label>
                    </div>
                    <div className="col-sm-6 col-lg-6 col-md-6">
                        <input type="text" placeholder="Enter your username" onChange={handleUserNameChange}></input>
                    </div>
                </div>
                <div className="col-sm-4 col-lg-4 col-md-4"></div>
            </div>
            <div className="row col-md-12 col-sm-12 col-lg-12">
                <div className="col-sm-4 col-lg-4 col-md-4"></div>
                <div className="col-sm-4 col-lg-4 col-md-4 row">
                    <div className="col-sm-6 col-lg-6 col-md-6 fields">
                        <label className="labelClass">Password</label>
                    </div>
                    <div className="col-sm-6 col-lg-6 col-md-6">
                        <input type="password" placeholder="Enter your password" onChange={handlePasswordChange}></input>
                    </div>
                </div>
            </div>
            <div className="col-md-12 col-sm-12 col-lg-12">
                <div className="login-div">
                    <button className="btn btn-primary login-btn" onClick={handleLogin}>Login</button>
                </div>
            </div>
            <div className="created-by col-md-12 col-sm-12 col-lg-12">Created by Athang using MERN stack<br />Nothing to worry even if your Records are Athang ;)</div>
        </div>
    )
}

export default Login
